  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav {
  background-color: #333;
  color: white;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.brand-logo {
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: none;
  color: white;
  min-width: 44px;
  min-height: 44px;
}

.menu-list {
  list-style: none;
  display: flex;
  gap: 20px;
}
li,
a {
  min-width: 44px;
  min-height: 44px;
  display: inline-block;
}
.menu-list li {
  padding: 10px;
}

.menu-list li a {
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.drawer {
  display: none;
  flex-direction: column;
  background-color: #333;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 80%;
  padding: 20px;
  box-sizing: border-box;
  z-index: 2;
  min-width: 44px;
  min-height: 44px;
}

.drawer a {
  text-decoration: none;
  color: white;
  font-size: 1.2rem;
  margin-bottom: 10px;
  min-width: 44px;
  min-height: 44px;
}

@media (max-width: 768px) {
  .menu-list {
    display: none;
  }

  .brand-logo {
    min-width: 44px;
    min-height: 44px;
  }

  .drawer {
    display: none;
    z-index: 3;
    min-width: 44px;
    min-height: 44px;
  }

  .drawer-button {
    display: block;
    font-size: 1.5rem;
    cursor: pointer;
  }
}

@media screen and (min-width: 769px) {
  .drawer-button {
    display: none;
  }
}

.hero {
  width: 100%;
  box-sizing: border-box;
}

.hero img {
  width: 100%;
  height: auto;
  display: block;
}

@media (min-width: 1200px) {
  .hero {
    min-width: 1000px;
  }
}

@media (max-width: 452px) {
  .hero {
    min-width: unset;
  }
}
img {
  display: block;
}
.restaurant-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  padding: 20px;
  margin-bottom: 70px;
}

.restaurant-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  width: 300px;
  position: relative;
  margin-top: 20px;
  z-index: 1;
}

.restaurant-img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.restaurant-info {
  padding: 15px;
}

.restaurant-name {
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 10px;
  text-decoration: none;
  color: gold;
}

.restaurant-city {
  font-style: italic;
  color: #f5f5f5;
  position: absolute;
  top: 8px;
  left: 16px;
  cursor: pointer;
  padding: 8px;
  background-color: #333;
  border: #333;
  border-radius: 5px;
}

.restaurant-city:hover {
  background-color: #ffc107;
}

.restaurant-rating {
  margin-top: 10px;
  display: flex;
  align-items: center;
}

.restaurant {
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
}

.restaurant__picture {
  width: 100%;
  max-width: 100%;
}

.restaurant__info h4 {
  margin: 8px 0;
}

.restaurant__tagline,
.restaurant__release-date,
.restaurant__duration,
.restaurant__rating {
  margin-top: 16px;
}

.restaurant__tagline p,
.restaurant__release-date p,
.restaurant__duration p,
.restaurant__rating p {
  margin: 4px 0;
}

.restaurant__overview {
  margin-top: 16px;
}

.restaurant__overview p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.restaurant__reviews {
  margin-top: 20px;
}

.review__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.review__name {
  font-weight: bold;
}

.review__date {
  color: #777;
}

.review__content {
  margin-bottom: 16px;
}

.reviewcs, .menuRestoran {
  list-style-type: none;
  display: block;
}

.menu {
  display: block;
}

.restaurant__menu-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}

.restaurant__foods,
.restaurant__drinks {
  margin-bottom: 20px; /* Mengatur jarak antar kolom */
}

.restaurant__foods ul,
.restaurant__drinks ul {
  padding: 0;
  list-style: none;
}

.restaurant__foods li,
.restaurant__drinks li {
  margin-bottom: 8px;
}
.star-icon {
  color: #ffc107;
  margin-right: 5px;
}

.skip-to-content {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}

.skip-to-content:focus {
  top: 0;
  list-style-type: none;
  text-decoration: none;
}

header {
  outline: none;
}

nav a:focus,
nav button:focus {
  outline: 2px solid #fff;
}

footer {
  text-align: center;
  padding: 10px;
  background-color: #333;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 2;
  color: white;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;

  position: fixed;
  bottom: 50px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}

.loading-indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.spinner {
  animation: rotate 1s linear infinite;
  width: 50px;
  height: 50px;
}

.path {
  stroke: #4285f4;
  stroke-linecap: round;
  animation: dash 1.5s ease-in-out infinite;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;EACE,UAAU;EACV,SAAS;EACT,sBAAsB;AACxB;;AAEA;;EAEE,SAAS;EACT,UAAU;EACV,WAAW;EACX,YAAY;AACd;;AAEA;EACE,gCAAgC;EAChC,SAAS;EACT,UAAU;EACV,sBAAsB;AACxB;;AAEA;EACE,sBAAsB;EACtB,YAAY;EACZ,kBAAkB;EAClB,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;EACjB,iBAAiB;EACjB,qBAAqB;EACrB,YAAY;EACZ,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,aAAa;EACb,SAAS;AACX;AACA;;EAEE,eAAe;EACf,gBAAgB;EAChB,qBAAqB;AACvB;AACA;EACE,aAAa;AACf;;AAEA;EACE,qBAAqB;EACrB,YAAY;EACZ,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,sBAAsB;EACtB,eAAe;EACf,MAAM;EACN,OAAO;EACP,YAAY;EACZ,UAAU;EACV,aAAa;EACb,sBAAsB;EACtB,UAAU;EACV,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,qBAAqB;EACrB,YAAY;EACZ,iBAAiB;EACjB,mBAAmB;EACnB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE;IACE,aAAa;EACf;;EAEA;IACE,eAAe;IACf,gBAAgB;EAClB;;EAEA;IACE,aAAa;IACb,UAAU;IACV,eAAe;IACf,gBAAgB;EAClB;;EAEA;IACE,cAAc;IACd,iBAAiB;IACjB,eAAe;EACjB;AACF;;AAEA;EACE;IACE,aAAa;EACf;AACF;;AAEA;EACE,WAAW;EACX,sBAAsB;AACxB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,cAAc;AAChB;;AAEA;EACE;IACE,iBAAiB;EACnB;AACF;;AAEA;EACE;IACE,gBAAgB;EAClB;AACF;AACA;EACE,cAAc;AAChB;AACA;EACE,aAAa;EACb,eAAe;EACf,SAAS;EACT,uBAAuB;EACvB,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,sBAAsB;EACtB,kBAAkB;EAClB,gBAAgB;EAChB,YAAY;EACZ,kBAAkB;EAClB,gBAAgB;EAChB,UAAU;AACZ;;AAEA;EACE,WAAW;EACX,aAAa;EACb,iBAAiB;AACnB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,iBAAiB;EACjB,iBAAiB;EACjB,mBAAmB;EACnB,qBAAqB;EACrB,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,cAAc;EACd,kBAAkB;EAClB,QAAQ;EACR,UAAU;EACV,eAAe;EACf,YAAY;EACZ,sBAAsB;EACtB,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,gBAAgB;EAChB,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,cAAc;EACd,WAAW;EACX,gBAAgB;EAChB,aAAa;EACb,0BAA0B;EAC1B,cAAc;AAChB;;AAEA;EACE,WAAW;EACX,eAAe;AACjB;;AAEA;EACE,aAAa;AACf;;AAEA;;;;EAIE,gBAAgB;AAClB;;AAEA;;;;EAIE,aAAa;AACf;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,uBAAuB;EACvB,oBAAoB;EACpB,qBAAqB;EACrB,4BAA4B;AAC9B;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,8BAA8B;E